.table-admin-container {
  background: rgb(241, 241, 218);
  width: 100%;
  align-self: center;
  height: calc(100vh - 260px);
  border-radius: 30px;
  overflow: auto;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
  z-index: 100;
}
.table-admin-container .table {
  margin-top: 30px;
  margin-right: 40px;
  margin-left: 40px;
  height: 94%;
}
